	module.exports = {
	  "content": "<p>下面的操作依赖于 <code>Node.js</code>，再进行下面的操作之前请确保你已经安装 Node.js 版本\n推荐使用最新的 Node.js 版本。</p>\n<div class=\"picidae-toc\">\n<ul>\n<li>\n<a href=\"#%E5%AE%89%E8%A3%85-picidae\">安装 Picidae</a>\n<ul>\n<li>\n<a href=\"#%E5%85%A8%E5%B1%80%E5%AE%89%E8%A3%85\">全局安装</a>\n</li>\n</ul>\n</li>\n<li>\n<a href=\"#%E5%88%9D%E5%A7%8B%E5%8C%96%E9%A1%B9%E7%9B%AE\">初始化项目</a>\n<ul>\n<li>\n<a href=\"#%E4%BF%AE%E6%94%B9-picidae-%E9%85%8D%E7%BD%AE\">修改 picidae 配置</a>\n</li>\n</ul>\n</li>\n</ul>\n</div>\n<hr>\n<h2 id=\"安装-picidae\"><a href=\"#%E5%AE%89%E8%A3%85-picidae\" aria-hidden=\"true\"><span class=\"icon icon-link\"></span></a>安装 Picidae</h2>\n<h3 id=\"全局安装\"><a href=\"#%E5%85%A8%E5%B1%80%E5%AE%89%E8%A3%85\" aria-hidden=\"true\"><span class=\"icon icon-link\"></span></a>全局安装</h3>\n<pre><code class=\"hljs language-bash\" data-query=\"{}\" data-lang=\"bash\">npm install picidae --global\n<span class=\"hljs-comment\"># 或者</span>\nyarn global add picidae </code></pre>\n<h2 id=\"初始化项目\"><a href=\"#%E5%88%9D%E5%A7%8B%E5%8C%96%E9%A1%B9%E7%9B%AE\" aria-hidden=\"true\"><span class=\"icon icon-link\"></span></a>初始化项目</h2>\n<pre><code class=\"hljs language-bash\" data-query=\"{}\" data-lang=\"bash\">picidae init my-docs\n<span class=\"hljs-built_in\">cd</span> my-docs\n<span class=\"hljs-comment\"># 全局安装 Haier</span>\npicidae use picidae-theme-haier\n<span class=\"hljs-comment\"># 或者本地安装 picidae 和 Haier</span>\nnpm install picidae picidae-theme-haier --save <span class=\"hljs-comment\"># 更推荐</span></code></pre>\n<h3 id=\"修改-picidae-配置\"><a href=\"#%E4%BF%AE%E6%94%B9-picidae-%E9%85%8D%E7%BD%AE\" aria-hidden=\"true\"><span class=\"icon icon-link\"></span></a>修改 picidae 配置</h3>\n<p>完成了上面的步骤后，可以看到 <code>my-docs</code> 目录下，产生了 <code>picidae.config.js</code> 配置文件。<br>\n下面将 picidae 配置中使用的主题修改为 Haier</p>\n<pre><code class=\"hljs language-javascript\" data-query=\"{}\" data-lang=\"javascript\"><span class=\"hljs-comment\">// picidae.config.js</span>\n<span class=\"hljs-built_in\">module</span>.exports = {\n  <span class=\"hljs-attr\">theme</span>: <span class=\"hljs-string\">'picidae-theme-haier'</span>,\n  <span class=\"hljs-attr\">docRoot</span>: <span class=\"hljs-string\">'./docs'</span>,\n  <span class=\"hljs-attr\">port</span>: <span class=\"hljs-number\">9999</span>\n}</code></pre>\n",
	  "extra": {}
	}

